import { createApp } from "vue";
import { createStore } from "vuex";
import router from "./router";
import App from "./App.vue";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap";
//FontAwesome
import { library } from "@fortawesome/fontawesome-svg-core";
//Solids
import {
  faHouseUser,
  faCalendarDays,
  faImage,
  faMapLocationDot,
} from "@fortawesome/free-solid-svg-icons";
//Brands
import {
  faSquareFacebook,
  faInstagram,
  faTiktok,
  faFortAwesome,
} from "@fortawesome/free-brands-svg-icons";
//Brands

// Add Fontawesome icons to app
library.add(
  faSquareFacebook,
  faInstagram,
  faTiktok,
  faHouseUser,
  faCalendarDays,
  faImage,
  faMapLocationDot,
  faFortAwesome
);

const store = createStore({
  state() {
    return {
      render: true,
    };
  },
});

const app = createApp(App);
app.use(store);
app.use(router);
app.mount("#app");
