import { createRouter, createWebHistory } from "vue-router";
import HomeVue from "../views/Home.vue";
import ProductVue from "../views/Product.vue";
import MediaVue from "../views/Media.vue";
import LocationVue from "../views/Location.vue";
import AboutUsVue from "../views/AboutUs.vue";
import ContactVue from "../views/Contact.vue";
import PolicyVue from "../views/Policy.vue";
import EventsVue from "../views/Events.vue";
import CompanyVue from "../views/Company.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeVue,
  },
  {
    path: "/about",
    name: "about",
    component: AboutUsVue,
  },
  {
    path: "/product",
    name: "product",
    component: ProductVue,
  },
  {
    path: "/media",
    name: "media",
    component: MediaVue,
  },
  {
    path: "/location",
    name: "location",
    component: LocationVue,
  },
  {
    path: "/contact",
    name: "contact",
    component: ContactVue,
  },
  {
    path: "/policy",
    name: "policy",
    component: PolicyVue,
  },
  {
    path: "/events",
    name: "events",
    component: EventsVue,
  },
  {
    path: "/company",
    name: "company",
    component: CompanyVue,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
